	module.exports = {"data":{"markdownRemark":{"html":"<img src=\"/static/images/darfur_wall.jpg\" alt=\"Darfur Wall\">\n<p>The <a href=\"http://darfurwall.org/\">Darfur Wall</a> is a beautifully executed charity project that fills a very simple, traditional purpose (collecting money) using an innovative and stark interface. The black and white, no-images design reinforces the tragedy of the situation without being overwhelming. I think this is a great example of online design serving a progressive cause — which is not so easy to find.</p>","frontmatter":{"created_at":"December 17, 2006","path":"/2006/12/17/the-darfur-wall","title":"The Darfur Wall","tags":["design","nptech","darfur"]}}},"pathContext":{}}

